@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'IBM Plex Sans', sans-serif;
}

.wrapper {
  width: 100%;
  margin: 0 auto;
}

.button {
  cursor: pointer;
  width: 100%;
  min-height: 56px;
  font-weight: 500;
  font-size: 16px;
  color: #EBF4F8;
  background: #0880AE;
  box-shadow: 0 2px 4px rgba(44, 39, 56, 0.08), 0 4px 8px rgba(44, 39, 56, 0.08);
  border: none;
  border-radius: 6px;
  transition: 0.2s;
}

.items-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.registration {
    background-color: #fff;
}

.registration .wrapper {
   padding: 40px 30px 10px 30px;
}

.registration__title {
    margin: 0 0 10px 0;
    font-weight: bold;
    font-size: 34px;
    line-height: 44px;
    color: #2C2738;
}

.registration__group {
    display: flex;
    align-items: center;
}

.registration__information {
    display: inline-block;
    padding: 0 0 3px 0;
    font-size: 16px;
    color: #2C2738;
}

.registration__btn {
    width: auto;
    width: initial;
    padding: 3px 6px;
    font-size: 16px;
    min-height: 22px;
    box-shadow: none;
    color: #0880AE;
    background-color: transparent;
}

.registration__btn:hover {
    color: #756F86;
}
